import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HomePage } from '../pages/home/home';
import { FeedbackPage } from '../pages/feedback/feedback';
import { AboutusPage } from '../pages/aboutus/aboutus';
import { EventsPage } from '../pages/events/events';
import { ForumPage } from '../pages/forum/forum';
import { MyprofilePage } from '../pages/myprofile/myprofile';
import { Storage }from '@ionic/storage';
import { OneSignalOriginal, OSNotificationPayload } from '@ionic-native/onesignal';  
import { isCordovaAvailable } from '../common/is-cordova-avaliable';  

//import { NewpagePage } from '../pages/newpage/newpage';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = HomePage;

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform, 
    public statusBar: StatusBar,
    public splashScreen: SplashScreen, private oneSignal: OneSignalOriginal, public storage:Storage) {
      this.platform.ready().then(() => {

        if (isCordovaAvailable()){  
          this.oneSignal.startInit('fab234a9-d23e-4530-8f3b-baadccd67fa2', '871625015250');  
          this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);  
          this.oneSignal.handleNotificationReceived().subscribe(data => this.onPushReceived(data.payload));  
          this.oneSignal.handleNotificationOpened().subscribe(data => this.onPushOpened(data.notification.payload));  
          this.oneSignal.endInit();  
        }  
        this.splashScreen.hide();
        this.storage.get('status').then(status=>{
          if(status=="true"){
            this.rootPage=EventsPage
          }
        })
      })
    this.initializeApp();
    
    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'My Profile', component: MyprofilePage},
      { title: 'Home', component: EventsPage },
      { title: 'Forum', component: ForumPage},
      { title: 'Feedback', component: FeedbackPage },
      { title: 'About Us', component: AboutusPage },
      { title: 'Logout', component: HomePage }      
    ];

  }
  private onPushReceived(payload: OSNotificationPayload) {  
    alert('Push recevied:' + payload.body);  
  }  
  
  private onPushOpened(payload: OSNotificationPayload) {  
    alert('Push opened: ' + payload.body);  
  }  

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



